.popupTableContainer {
    position: absolute;
    overflow: auto;
    z-index: 2;
}

.popupTableContainer table {
    display: block;
    border-collapse: collapse;
    width: 100%;
    margin: 0;
}

.popupTableContainer th,
.popupTableContainer td {
    padding: 8px;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: min(1em, calc(100% - 8px));
}

.popupTableContainer th {
    background-color: #f2f2f2;
    font-weight: bold;
}
